/**
 * 
 * @param {*} fn 
 * @param {*} delay
 */
export function Throttle (fn, delay) {
  var timer;
  return function () {
      var _this = this;
      // eslint-disable-next-line prefer-rest-params
      var args = arguments;
      if (timer) {
          return;
      }
      timer = setTimeout(function () {
          fn.apply(_this, args);
          timer = null; // delayfntimertimerthrottle
      }, delay);
  };
}
